<template>
  <a-form :form="bookForm" :label-col="{span: 6}" :wrapper-col="{span: 12}" @submit="doSubmit">
    <a-tabs
      defaultActiveKey="1"
      :tabPosition="tabMode"
    >
      <!-- 书籍信息 -->
      <a-tab-pane tab="书籍" key="1">
        <a-row>
          <a-col :span="12">
            <a-form-item label="类型" >
              <a-select :options="bookType" :value="bookData.type" :disabled="viewMode" @change="changeBookType"></a-select>
            </a-form-item>
          </a-col>
          <a-col :span="12">
            <a-form-item label="书名" >
              <a-input id="name" :disabled="viewMode" v-model="bookData.name"/>
            </a-form-item>
          </a-col>
        </a-row>
        <a-row>
          <a-col :span="12">
            <a-form-item label="书库位置">
              <a-input id="location" :disabled="viewMode" v-model="bookData.location"/>
            </a-form-item>
          </a-col>
          <a-col :span="12">
            <a-form-item label="价格">
              <a-input id="price" :disabled="viewMode" v-model="bookData.price"/>
            </a-form-item>
          </a-col>
        </a-row>
        <a-row>
          <a-col :span="12">
            <a-form-item label="预览图">
              <a-input id="preview" :disabled="viewMode" v-model="bookData.preview"/>
            </a-form-item>
          </a-col>
          <a-col :span="12">
          </a-col>
        </a-row>
      </a-tab-pane>
      <!-- 作者信息 -->
      <a-tab-pane tab="作者" key="2">
        <a-row>
          <a-col :span="12">
            <a-form-item label="作者">
              <a-input id="author.name" :disabled="viewMode" v-model="bookData.author.name"/>
            </a-form-item>
          </a-col>
          <a-col :span="12">
            <a-form-item label="年龄">
              <a-input id="author.age" :disabled="viewMode" v-model="bookData.author.age"/>
            </a-form-item>
          </a-col>
        </a-row>
      </a-tab-pane>
      <!-- 出版信息 -->
      <a-tab-pane tab="出版信息" key="3">
        <a-row>
          <a-col :span="12">
            <a-form-item label="出版商">
              <a-input id="publish.publisher" :disabled="viewMode" v-model="bookData.publish.publisher"/>
            </a-form-item>
          </a-col>
          <a-col :span="12">
            <a-form-item label="出版日期">
              <a-input id="publish.publishDay" :disabled="viewMode" v-model="bookData.publish.publishDay"/>
            </a-form-item>
          </a-col>
        </a-row>
      </a-tab-pane>
    </a-tabs>
    <div style="border: 0px red solid; text-align: center;">
      <a-button type="primary" html-type="submit">
        submit
      </a-button>
    </div>
  </a-form>
</template>

<script>
import {books} from '../dataSample.js'
import {bookType} from '../constants.js'

export default {
  data() {
    return {
      tabMode: 'left',
      viewMode: false,
      bookData: null,
      bookType: bookType,
      // bookForm: this.$form.createForm(this, {}),
    };
  },
  beforeCreate() {
    // window.console.log(this.$route.params);
    this.bookForm = this.$form.createForm(this, {});
  },
  created () {
    this.queryBookById()
  },
  beforeMount: function() {
    let matchedBreadcrumb = this.$store.state.breadcrumbs.filter(b => b.path === this.$route.path);
    if (matchedBreadcrumb && matchedBreadcrumb.length > 0) {
      this.$store.commit('removeBreadcrumb', this.$store.state.breadcrumbs.indexOf(matchedBreadcrumb[0]));
    } else {
      this.$store.commit(
        'addBreadcrumb', 
        {
            path: this.$route.path, 
            breadcrumbName: '编辑图书',
        }
      );
    }
  },
  methods: {
    queryBookById () {
      // 取数据，可以改成调用api
      let bookByIds = books.filter(b => (b.id === parseInt(this.$route.params.id)));
      if (!bookByIds || bookByIds.length <= 0) {
        window.console.log("没有找到对应的书籍");
      }
      this.bookData = bookByIds[0];
    },
    changeBookType(value) {
      // window.console.log(value);
      this.bookData.type = value;
    },
    doSubmit(e) {
      // 提交 this.bookData
      // window.console.log(this.bookData);
    },
  }
};
</script>