<template>
<div>
  <!-- 查询条件 -->
  <div class="query-conditions">
    <a-form :form="bookQueryFrom" :layout="formLayout" @submit="queryBooks">
      <a-row>
        <a-col :span="12">
          <a-form-item
            label="书名"
            :label-col="formItemLayout.labelCol"
            :wrapper-col="formItemLayout.wrapperCol"
          >
            <!-- 用v-decorator注册字段，或者定义一个对象，用v-model双向绑定其中的字段 -->
            <a-input placeholder="输入书名，模糊查询" 
              v-decorator="['bookName', { rules: [{ required: true, message: '书名必填' }] }]"/>
          </a-form-item>
        </a-col>
        <a-col :span="12">
          <a-form-item
            label="出版年份"
            :label-col="formItemLayout.labelCol"
            :wrapper-col="formItemLayout.wrapperCol"
          >
            <a-input placeholder="输入出版年份" 
              v-decorator="['publishYear', { rules: [{pattern: /^\d{4}$/, message: '输入4位年份' }] }]"/>
          </a-form-item>
        </a-col>
      </a-row>
      <a-row>
        <a-col :span="12">
          <a-form-item
            label="作者"
            :label-col="formItemLayout.labelCol"
            :wrapper-col="formItemLayout.wrapperCol"
          >
            <a-input placeholder="输入作者，模糊查询" 
              v-decorator="['authorName', {}]"/>
          </a-form-item>
        </a-col>
      </a-row>
      <a-form-item :wrapper-col="buttonItemLayout.wrapperCol">
        <a-button type="primary" html-type="submit">查询</a-button>
      </a-form-item>
    </a-form>
  </div>

  <!-- 数据展示 -->
  <div class="data-display">
    <a-table :columns="columns" :dataSource="data" :rowKey="record => record.id">
      <template slot="action" slot-scope="text, record">
        <!-- 两种vuerouter的路由方式，1.详情功能里面通过编程式导航，用this.$router.push；2.编辑功能里面的router-link组件。
            两个是等价的，router-link点击后实际就是用了编程式导航 
        -->
        <a href="javascript:;" @click="() => queryBookById(record.id, text)">详情</a>
        <a-divider type="vertical" />
        <router-link :to="'/book/edit/' + record.id">编辑</router-link>
        <a-divider type="vertical" />
        <a-popconfirm
          title="删除后无法恢复，确定删除吗？"
          @confirm="deleteBook(record.id, text)"
          okText="确定"
          cancelText="放弃"
        >
          <a href="javascript:;">删除</a>
        </a-popconfirm>
      </template>
    </a-table>
  </div>
</div>
</template>

<script>

import {bookType} from '../constants.js'

  const columns = [
    {
      title: '类型',
      dataIndex: 'type',
      key: 'type',
      customRender: (text, record, index) => {
        // window.console.log(text, record, index)
        // 从value转成lable
        return bookType.filter(t => t.value === text)[0].label
      }
    },
    {
      title: '书名',
      dataIndex: 'name',
      key: 'name',
    },
    {
      title: '作者',
      dataIndex: 'author.name',
      key: 'author.name',
    },
    {
      title: '出版社',
      key: 'publish.publisher',
      dataIndex: 'publish.publisher',
    },
    {
      title: '出版日期',
      key: 'publish.publishDay',
      dataIndex: 'publish.publishDay',
    },
    {
      title: '书库位置',
      key: 'location',
      dataIndex: 'location',
    },
    {
      title: '价格',
      key: 'price',
      dataIndex: 'price',
    },
    {
      title: '预览图',
      key: 'preview',
      dataIndex: 'preview',
    },
    {
      title: '操作',
      key: 'action',
      dataIndex: 'action',
      scopedSlots: {
        customRender: 'action'
      }
    },
  ];

  import {books} from '../dataSample.js';

  export default {
    data() {
      return {
        data: books,
        columns,
        formLayout: 'inline',
        bookQueryFrom: this.$form.createForm(this, {}),
      };
    },
    computed: {
      formItemLayout() {
        const { formLayout } = this;
        return formLayout === 'horizontal'
          ? {
              labelCol: { span: 4 },
              wrapperCol: { span: 14 },
            }
          : {};
      },
      buttonItemLayout() {
        const { formLayout } = this;
        return formLayout === 'horizontal'
          ? {
              wrapperCol: { span: 14, offset: 4 },
            }
          : {};
      },
    },
    methods: {
      queryBooks(e) {
        // 校验错误
        this.bookQueryFrom.validateFields((err, values) => {
          if (!err) {
            // values就是查询条件form里面的值
            window.console.log('Received values of form: ', values);
            // 提交查询请求queryValues
          }
        });
      },
      queryBookById(bookId) {
        this.$router.push('/book/' + bookId);
      },
      deleteBook(bookId) {
        alert('deleteBook:' + bookId);
      }
    },
    beforeMount: function() {
      let matchedBreadcrumb = this.$store.state.breadcrumbs.filter(b => b.path === this.$route.path);
      if (matchedBreadcrumb && matchedBreadcrumb.length > 0) {
        this.$store.commit('removeBreadcrumb', this.$store.state.breadcrumbs.indexOf(matchedBreadcrumb[0]));
      } else {
        this.$store.commit(
          'addBreadcrumb', 
          {
              path: this.$route.path, 
              breadcrumbName: '图书',
          }
        );
      }
    }
  };
</script>
